export default {
	data() {
		return {
			isSearch: false,
			showOper: false, // 发布的操作
			showPic: false, // 打开图片无需重新加载数据
			keywords: '',
			prodhistoryList: [],
			hotSearch: [],
			nosearch: true,
			dataList: [],
			pagesize: 100,
			pageIndex: 1,
			pageTotal: 1,
			nodata: false
		};
	},
	onShow() {
		this.initData();
	},
	onPullDownRefresh() {
		this.keywords = ''
		this.pageIndex = 1
		this.dataList = []
		this.initData();
		uni.stopPullDownRefresh();
	},
	methods: {
		initData() {
			let self = this
			let params = {
				keywords: this.keywords,
				pageIndex: this.pageIndex,
				pageSize: this.pageSize
			}
			self.$acFrame.HttpService.shopList(params).then(res => {
				if (res.success) {
					let list = res.data.rows
					self.pageTotal = res.data.pageTotal
					if (list.length > 0) {
						self.nodata = false
						self.dataList = list
					} else {
						self.nodata = true
						self.dataList = []
					}
					if (self.isSearch) {
						self.nosearch = false
					}
				} else {

				}
			})
		},
		searchClick() {
			let self = this
			this.isSearch = true;
			this.dataList = []
			this.prodhistoryList = uni.getStorageSync('prodhistoryList') || [];
			this.$acFrame.Util.getHotList(2).then(res => {
				if (res.success) {
					self.hotSearch = res.data
				}
			})
		},
		cancelSearch() {
			this.isSearch = false;
			this.searchVal = false;
			this.nosearch = true

			this.resetData();
			this.initData()
		},
		clearVal() {
			this.keywords = ""
		},
		searchData() {
			let val = this.keywords;
			this.pageIndex = this.pageIndex,
				this.pageSize = this.pageSize
			this.pageType = null;
			let prodhistoryList = uni.getStorageSync('prodhistoryList') || [];
			let goon = true
			prodhistoryList.filter(v => {
				if (v == val) {
					goon = false
				}
			})
			if (goon) {
				if (prodhistoryList.length < 10) {
					if (prodhistoryList.length > 0) {
						prodhistoryList.unshift(val);
					} else {
						prodhistoryList.push(val);
					}

				} else {
					prodhistoryList.splice(9, 1);
					prodhistoryList.unshift(val);
				}
			}
			uni.setStorageSync('prodhistoryList', prodhistoryList);
			this.prodhistoryList = prodhistoryList
			this.initData();
		},
		//清空历史
		clearHistory() {
			uni.setStorageSync('prodhistoryList', []);
			this.prodhistoryList = []
		},
		tapSearch(val, type) {
			if (type && type == 'history') {
				let prodhistoryList = uni.getStorageSync('prodhistoryList');
				prodhistoryList.filter((v, i) => {
					if (v == val) {
						prodhistoryList.splice(i, 1);
						prodhistoryList.unshift(val)
					}
				})
				this.prodhistoryList = prodhistoryList
				uni.setStorageSync('prodhistoryList', prodhistoryList);
			}
			this.resetData();
			this.pageType = null;
			this.keywords = val;
			this.initData();
		},
		shopDetail(id) {

			wx.navigateTo({
				url: `shopDetail?id=${id}`
			})
		},
		productDetail(id) {
			console.log(id);
			wx.navigateTo({
				url: "productDetail?id=" + id
			})
		},
		resetData() {
			this.pageIndex = 1;
			this.pageSize = 10;
			this.dataList = [];
			this.keywords = ''
			this.nodate = false;
			this.nomore = false
		},
		setImg(src) {
			return this.$acFrame.Util.setImgUrl(src);
		}
	}
};
